.nav {
  margin-bottom: 6.25rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}
@media (min-width: 40em) {
  .nav {
    margin-bottom: 6.875rem;
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}
.nav__logo {
  display: inline-block;
  font-size: 0;
}
.nav__logo img {
  width: 8.75rem;
  height: 1.5rem;
}
@media (min-width: 40em) {
  .nav__logo img {
    width: 10.3125rem;
    height: 1.75rem;
  }
}
@media (min-width: 71.875em) {
  .nav__logo img {
    width: 14.6875rem;
    height: 2.3125rem;
  }
}
.nav__btn {
  display: inline-block;
  border: 0.0625rem solid;
  border-radius: 3.125rem;
  padding: 0.375rem 1.25rem;
  font-size: 0.75rem;
  color: var(--lt-pink);
  transition: all 300ms ease-in-out;
}
.nav__btn:hover {
  border: 0.0625rem solid var(--lt-pink);
  background-color: var(--lt-pink);
  color: var(--white);
}
@media (min-width: 40em) {
  .nav__btn {
    border-radius: 3.125rem;
    padding: 0.625rem 1.875rem;
    font-size: 1rem;
  }
}